:root {
  --primary: #d81e5b;
  --secondary: #9588c6;
  --dark: #470b7a;
  --light: #eee;
  --green: #470b7a;
  --red: red;
}

@font-face {
  font-family: "Digit";
  src: url("font/Matricha.ttf");  
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;  
}

body {
  background-color: var(--light);  
  background-size: 30px 30px;
  background-image:
    linear-gradient(to right, lightgrey 1px, transparent 1px),
    linear-gradient(to bottom, lightgrey 1px, transparent 1px);
}

.equation {
  color: var(--dark);
  font-size: 25px;
  top: -10px;
  margin: 0px;
  padding: 0px;
  font-family: Digit;
  transform: translateY(-25px);
}

header {
  position: absolute;
  width:100%;
  display: flex;
  align-items: center;
  justify-content:space-between;
  height: 40px;
  padding: 0 10px;
  background-color: var(--dark);
  color: var(--light);
  z-index: 1;
}

nav a {
  margin: 0 10px;
  color: var(--light);
  text-decoration: none;
  font-weight: 100;
}

nav a:hover {
  font-weight: 800;  
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  filter: none;
  border: none;
  outline: none;
  color: var(--light);
  visibility: hidden;
  opacity: 0;
  font-size: 20px;  
}

@media only screen and (max-width: 724px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;    
  }

  header .nav-btn:hover {
    background: none;
  }

  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    background-color: var(--dark);
    transition: 1s;
    transform: translateY(-100vh);
  }

  header .responsive_nav {
    transform: none;
  }

  header .nav-close-btn {
    position: absolute;
    top:20px;
    right: 20px;
  }

  header .nav-close-btn:hover {
    background: none;
  }

  nav a {
    font-size: 20px;
  }

}

.App {
  align-items:center;
  display:flex;
  justify-content:center;
  min-height:100vh;
  padding:16px
}

.calculator {
  width: 100%;
  max-width: 400px;
  background-color: var(--secondary);
  border-radius: 16px;
  border: 2px solid;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 14px;
  text-align: right;
  background: rgb(183,222,237); /* Old browsers */
  background: -moz-linear-gradient(top,  rgba(183,222,237,1) 0%, rgba(113,206,239,1) 39%, rgb(112, 197, 223) 62%, rgba(183,222,237,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  rgba(183,222,237,1) 0%,rgba(113,206,239,1) 39%,rgba(122,197,223,1) 62%,rgba(183,222,237,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  rgba(183,222,237,1) 0%,rgba(113,206,239,1) 39%,rgba(122,197,223,1) 62%,rgba(183,222,237,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b7deed', endColorstr='#b7deed',GradientType=0 ); /* IE6-9 */
  border: 2px solid;
  color: var(--dark);
  font-family: Digit;
  min-height: 160px;
  font-weight: 500;
  text-overflow: ellipsis;
  border-radius: 16px;
  margin: 8px;    
  line-height: auto;
}

.display .displayTitle {
  font-size: 35px;
  line-height: 100%;
  padding-bottom: 0px;
  color: var(--dark);
}

.displayTitleFrac {
  font-size: 35px;
  line-height: 100%;
  padding-bottom: 20px;
  font-family: Digit;
}

.displayTitleFrac span {
  font-family: Digit;
  font-size: 35px;
  top: 0;
  margin-top: 0;
  padding-top: 0;
  font-weight: 500;
  color: var(--red);  
  line-height: 100%;  
}

.display span {
  font-family: Digit;
  font-size: 35px;
  font-weight: 500;
  color: var(--red);  
  line-height: 100%;  
}

.display .addText {
  font-size: 20px;  
  color: var(--dark);
  line-height: 20%;
  line-height: 100%;  
}

button {
  appearance: none;
  border: none;
  outline: none;
  color: #FFF;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
  border: 1px solid;
}

button:hover {
  background-image: linear-gradient(to right top, #bda0ba, #ab83ac, #98689f, #834d93, #6b3288, #6b3288, #6b3288, #6b3288, #834d93, #98689f, #ab83ac, #bda0ba);
}

.operators {
  display: flex;
}

#operators {
  border-radius: 16px;
  margin: 8px;
  background-color: var(--primary);
  font-weight: 700;
  letter-spacing: 0.1em;
}

#operators:hover {
  background-image: linear-gradient(to right top, #bda0ba, #c28eb2, #c97aa6, #d06594, #d54c7e, #d54c7e, #d54c7e, #d54c7e, #d06594, #c97aa6, #c28eb2, #bda0ba);
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  border-radius: 16px;
  margin: 8px;
  flex: 1 1 28%;
  
  background-color: var(--dark);
}

.head {
  display: flex;
  flex-wrap: wrap;
}

.head button {  
  border-radius: 16px;
  margin: 8px;
  margin-bottom: 0px;
  cursor: none; 
  background-color: var(--dark);
}

.head button {    
  flex: 1 1 28%;  
}

.head-text  {  
  display: flex;
  flex-wrap: wrap;  
  color: var(--dark);
}

.head-text div {    
  text-align: center;
  border-radius: 16px;
  margin: 8px;
  margin-top: 0px;
  cursor: none; 
  color: var(--white);
  margin-bottom: 10px;
  flex: 1 1 28%;  
}

footer {
  position: absolute;
  width:100%;
  align-items: center;
  height: 40px;
  margin-top: -40px;  
  background-color: var(--dark);
  color: var(--light);
  text-decoration: none;  
  z-index: 0;
}

.footer-p {
  box-sizing: border-box;
  text-align: center;  
  font-weight: 100;
  font-size: 15px;
  padding-top: 10px;
}

.footer-p a {
  color: var(--white);
  text-decoration: none;
}

.Menu {
  display:inline-block;
  text-align:center;
  align-items:center;
  display:flex;
  justify-content:center;
  min-height:100vh;
  padding:16px;
  flex-direction: column;
}

.Menu::before{
  content:'';
  display:inline-block;
  height:100%;
  vertical-align:middle;
  width:0px;
}

.Menu a {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 20px;
  font-family: Digit;
  font-size: 30px;
  font-weight: 600;
  text-decoration: none;
  
}
